<template>
  <div class="needForm">
    <div class="title">
      需求详情
    </div>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="*需求类别">
        <div class="detail-form">
          <el-input v-model="form.demandType"  :disabled="true"> </el-input>
        </div>
      </el-form-item>
      <el-form-item label="*合作方式">
        <div class="detail-form">
          <el-input v-model="form.cooperationType"  :disabled="true"> </el-input>
        </div>
      </el-form-item>
      <el-form-item label="*需求行业">
        <div class="detail-form">
          <div style="display: inline-block;width: 105px;height: 40px;">
            <el-input
              placeholder=""
              v-model="form.demandIndustry"
              :disabled="true">
            </el-input>
          </div>
          <div style="display: inline-block;width: 189px;height: 40px;">
            <el-input
              placeholder=""
              v-model="form.demandIndustryDetail"
              :disabled="true">
            </el-input>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="*需求概述">
        <div class="detail-form">
          <el-input v-model="form.Summary"  :disabled="true"> </el-input>
        </div>
      </el-form-item>
      <el-form-item label="*需求详情(限500字内)">
        <div class="detail-form">
          <el-input type="textarea" :disabled="true" v-model="form.desc" :autosize="{ minRows: 6}" maxlength="500"  show-word-limit></el-input>
        </div>
      </el-form-item>
      <el-form-item label="*期望实现结果">
        <div class="detail-form">
          <el-input :disabled="true" v-model="form.result"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="*需求企业名称">
        <div class="detail-form">
          <el-input :disabled="true" v-model="form.name"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="*所属市县">
        <div style="display: inline-block;width: 105px;height: 40px;">
          <el-input
            placeholder=""
            v-model="form.city"
            :disabled="true">
          </el-input>
        </div>
        <div style="display: inline-block;width: 105px;height: 40px;">
          <el-input
            placeholder=""
            v-model="form.district"
            :disabled="true">
          </el-input>
        </div>
      </el-form-item>
      <el-form-item label="*联系人">
        <div class="detail-form">
          <el-input :disabled="true" v-model="form.contact"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="*电话">
        <div class="detail-form">
          <el-input :disabled="true" v-model="form.phone"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="*邮箱">
        <div class="detail-form">
          <el-input :disabled="true" v-model="form.mail"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="*需求截止时间">
        <div class="detail-form">
          <el-input
            placeholder=""
            v-model="form.endDate"
            :disabled="true">
          </el-input>
        </div>
      </el-form-item>
      <el-form-item label="备注" >
        <div class="detail-form">
          <el-input :disabled="true" type="textarea" v-model="form.remark" :autosize="{ minRows: 6}"></el-input>
        </div>
      </el-form-item>
      <el-form-item>
        <input type="button" value="关闭" @click="onSubmit" class="close">
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
    export default {
        name: "Details",
        data() {
            return {
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    phone:'',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: '',
                    mail:'',
                    result:'',
                    Summary:'',
                    contact:'',
                    remark:'',
                    demandIndustry:'',
                    demandIndustryDetail:'',
                    endDate:'',
                    city:'',
                    district:'',
                    demandType:'',
                    cooperationType:''
                },
                selectedOptions: []
            }
        },
        created(){
            this.getForm();
        },
        methods: {
            onSubmit() {
                console.log('submit!');
                this.$emit('fathermethod');
            },
            handleChange (value) {
                console.log(value)
            },
            //获取表单信息
            getForm(){
               var that = this;
               var id = that.$store.state.id;
               that.$axios.get('http://192.168.1.104:8088/demands/getDemandsByID',{
                   params:{
                       id:id
                   }
               })
                   .then((res)=>{
                       console.log(res)
                       if (res.data.code==0){
                           that.form.Summary = res.data.data.demandOutline;
                           that.form.desc = res.data.data.demandContent;
                           that.form.result = res.data.data.expectedResult;
                           that.form.name = res.data.data.companyName;
                           that.form.contact = res.data.data.contact;
                           that.form.phone = res.data.data.phoneNum;
                           that.form.mail = res.data.data.email;
                           that.form.remark = res.data.data.remark;
                           that.form.demandIndustry = res.data.data.demandIndustry;
                           that.form.demandIndustryDetail = res.data.data.demandIndustryDetail;
                           that.form.remark = res.data.data.remark;
                           that.form.endDate = res.data.data.endDate;
                           that.form.city = res.data.data.city;
                           that.form.district = res.data.data.district;
                           that.form.demandType = res.data.data.demandType;
                           that.form.cooperationType = res.data.data.cooperationType;
                       }
                   })
            }
        }
    }
</script>

<style  lang="less">
  .el-form-item__content {
    margin-left: 170px!important;
  }
  .el-form-item__label {
    width: 150px!important;
  }
  .needForm {
    width: 685px;
    margin: 0 auto;
    .title {
      font-size: 18px;
      color: #333333;
      margin-top: 28px;
      margin-bottom: 29px;
      font-weight: 600;
      text-align: center;
    }
  }
  .needWarp {
    text-align: center;
    vertical-align: top;
    .warpL {
      width: 150px;
      height: 100%;
      display: inline-block;
      background-color: green;
      ul {
        li {
          list-style: none;
        }
      }
    }
    .warpR {
      width: 490px;
      height: 100%;
      display: inline-block;
      background-color: blue;
      vertical-align: top;
      ul {
        li {
          text-align: left;
          list-style: none;
        }
      }
    }
  }
  .detail-form {
    width: 300px;
  }
  .close {
    width: 300px;
    height: 40px;
    background: #169bd5;
    border: none;
    outline: none;
    font-size: 13px;
    color: #FFFFFF;
    border-radius: 5px;
    cursor: pointer;
  }
</style>



