<template>
  <div>
    <div style="width: 1050px;margin: 0 auto">
      <div style="width:500px;height:500px;display: inline-block;" ref="chart" ></div>
      <div style="width:500px;height:500px;display: inline-block;" ref="chart1"></div>
    </div>

  </div>
</template>

<script>
    export default {
        name: "Achievement",
        data(){
            return {
                option:{
                    title : {
                        text: '企业需求行业占比统计',
                        x:'center'
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    series : [
                        {
                            name: '访问来源',
                            type: 'pie',
                            radius : '55%',
                            center: ['50%', '60%'],
                            data:'',
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                },
                option1:{
                    title : {
                        text: '企业需求增长统计',
                        x:'center'
                    },
                    xAxis: {
                        type: 'category',
                        data: ''
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: '',
                        type: 'line'
                    }]
                }
            }
        },
        methods:{
            //初始化e-charts
            initCharts () {
                let myChart = this.$echarts.init(this.$refs.chart);
                console.log(this.$refs.chart)
                // 绘制图表
                myChart.setOption(this.option);
            },
            //初始化e-charts1
            initCharts1 () {
                let myChart = this.$echarts.init(this.$refs.chart1);
                console.log(this.$refs.chart1)
                // 绘制图表
                myChart.setOption(this.option1);
            },
            //获取饼图数据
            getPieInfo(){
                var that = this;
                console.log('获取饼图数据')
                this.$axios.get('http://192.168.1.104:8088/demands/demandsIndustryProp')
                    .then((res)=>{
                        if (res.data.code==0){
                            var arr = [];
                            res.data.data.sdata.forEach((item,key)=>{
                                arr.push({value:item.value,name:item.name})
                            })
                            console.log(arr)
                            that.option.series[0].data = arr;
                            that.initCharts();
                        }
                        console.log(res)
                    })
            },
            //获取折线图数据
            getCharts1(){
                var that = this;
                this.$axios.get('http://192.168.1.104:8088/demands/demandsRiseTrend')
                    .then((res)=>{
                        if (res.data.code==0){
                           that.option1.series[0].data = res.data.data.sdata;
                            that.option1.xAxis.data = res.data.data.xdata;
                        }
                        console.log(res);
                        that.initCharts1();
                    })
            }

        },
        mounted() {
            this.getPieInfo();
            this.getCharts1();
        },
        created() {
        }
    }
</script>

<style scoped lang="less">

</style>



