            },
            //获取页码数
            getSize(a){
                console.log(a)
            },
            //获取科技需求信息
            getTecInfo(val){
              var that = this;
              var val = val||1;
              that.$axios.get('http://192.168.1.104:8088/demands/showByPage',{
                  params:{
                      pageSize:'10',
                      pageNum:val
                  }
              }).then((res)=>{
                  console.log(res.data.code)
                  if (res.data.code==0){
                      console.log(res.data.data.total)
                      that.totalPages = res.data.data.total;
                      console.log(res.data.data.list)
                      that.TecInfo = res.data.data.list;
                  }
                  console.log(res)
              })
            },
            handleChange1(){

            },
            closeDetails(){
                this.ifDetails = false;
            },
            toDetail(i){
                console.log(i);
                this.$store.state.id = i;
                console.log(this.$store.state.id);

                this.ifDetails =true;
            },
            //打开留言
            openMessage(i,id){
                console.log(this.Message)
                //获取留言信息
                console.log('id',id)
                this.input = '';
                var that = this;
                that.$axios.get('http://192.168.1.104:8088/demandsComment/showCommentByPage',{
                    params:{
                        demandsID:id,
                        pageNum:1,
                        pagesize:10
                    }
                }).then((res)=>{
                    if (res.data.code==0){
                      that.leaveMessage[i] = res.data.data.list;
                      console.log(that.leaveMessage)
                        console.log(i)
                        that.Message[i+1] =  !this.Message[i+1];
                    }
                    console.log(res)
                })
            }
        },
        directives: {
            //拖拽&&缩放指令
            drag: {
                bind: (el, binding) => {
                    //绑定默认样式
                    el.style.zIndex = 9;
                    //如果为编辑状态
                    if (binding.value || binding.value === undefined) {
                        //定义该元素的 top left width height
                        let x, y, w, h;
                        //鼠标的起始和结束坐标
                        let cx_start, cy_start, cx_end, cy_end;
                        //判断鼠标样式
                        el.onmousemove = e => {
                            //获取鼠标当前位置
                            let cx_now = e.clientX;
                            let cy_now = e.clientY;
                            //获取div右下角相对浏览器的位置
                            let {
                                top: el_top,
                                left: el_left,
                                width: el_width,
                                height: el_height
                            } = el.getBoundingClientRect();
                            let el_bottom_height = el_top + el_height;
                            let el_right_width = el_left + el_width;
                            //判断鼠标是否在div下边界
                            let mouse_in_bottom =
                                cy_now <= el_bottom_height + 5 && cy_now >= el_bottom_height - 5;
                            //判断鼠标是否在div右边界
                            let mouse_in_right =
                                cx_now <= el_right_width + 5 && cx_now >= el_right_width - 5;
                            if (mouse_in_bottom && mouse_in_right) {
                                el.style.cursor = "se-resize";
                            } else if (mouse_in_right) {
                                el.style.cursor = "e-resize";
                            } else if (mouse_in_bottom) {
                                el.style.cursor = "s-resize";
                            } else {
                                el.style.cursor = "move";
                            }
                        };
                        el.onmousedown = e => {
                            let mouse = el.style.cursor;
                            //更改默认样式
                            el.style.zIndex = 99;
                            //对象解构赋值
                            let {left: el_x, top: el_y, width: el_w, height: el_h} = window.getComputedStyle(el);
                            x = el_x;
                            y = el_y;
                            w = el_w;
                            h = el_h;
                            cx_start = e.clientX;
                            cy_start = e.clientY;
                            //绑定移动事件
                            document.onmousemove = e => {
                                cx_end = e.clientX;
                                cy_end = e.clientY;
                                //默认左下方向配置
                                let x_move = cx_end - cx_start;
                                let y_move = cy_end - cy_start;
                                let direct = ["width", "height"];
                                let pos = [w, h];
                                let move = [x_move, y_move];
                                let limit = 50;
                                //判断鼠标的类型进行对应的操作
                                switch (mouse) {
                                    case "e-resize":
                                        direct = ["width"];
                                        pos = [w];
                                        move = [x_move];
                                        break;
                                    case "s-resize":
                                        direct = ["height"];
                                        pos = [h];
                                        move = [y_move];
                                        break;
                                    case "move":
                                        direct = ["left", "top"];
                                        pos = [x, y];
                                        limit = 0;
                                        break;
                                }
                                handle_div(direct, pos, move, limit);
                            };
                            //取消移动事件
                            document.onmouseup = e => {
                                //还原默认样式
                                el.style.zIndex = 9;
                                document.onmousemove = null;
                            };

                            /**
                             * 操作DOM位置和大小方法
                             * @param direct 方向
                             * @param pos 尺寸/坐标
                             * @param move 拖动距离
                             * @param limit 限定范围
                             */
                            function handle_div(direct, pos, move, limit) {
                                for (let i = 0; i < direct.length; i++) {
                                    let val = parseInt(pos[i]) + move[i];
                                    val = val <= limit ? limit : val;
                                    el.style[direct[i]] = val + "px";
                                }
                            }
                        };
                    } else {
                        el.style.cursor = "default";
                        //移除点击事件
                        el.onmousedown = null;
                        el.onmousemove = null;
                    }
                }
            }
        }
    }
</script>

<style scoped lang="less">
  .message {
    width: 1009px;
    background-color: rgba(129, 211, 248, 0.192156862745098);
    margin: 0 auto;
