  }
  .needWarp {
    div {
      ul {
        text-align: center;

        li {
          display: inline-block;
          font-size: 14px;
          color: #333333;
          height: 42px;
          line-height: 42px;
        }
        li:nth-child(1){
          width: 152px;
        }
        li:nth-child(2){
          width: 275px;
        }
        li:nth-child(3){
          width: 108px;
        }
        li:nth-child(4){
          width: 108px;
        }
        li:nth-child(5){
          width: 126px;
        }
        li:nth-child(6){
          width: 165px;
        }
        li:nth-child(7){
          width: 120px;
        }
      }
    }
  }
  .details {
    position: absolute;
    width: 691px;
    height: 1200px;
    border: 1px solid rgba(121, 121, 121, 1);
    top: 3px;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(255, 255, 255, 1);
  }
</style>



