                if (companyName==''){
                    this.$message({
                        message: '请填写需求企业名称!',
                        type: 'warning'
                    });
                    return false
                }

                if (city==''){
                    this.$message({
                        message: '请选择城市!',
                        type: 'warning'
                    });
                    return false
                }
                if (district==''){
                    this.$message({
                        message: '请选择区县!',
                        type: 'warning'
                    });
                    return false
                }
                if (contact==''){
                    this.$message({
                        message: '请填写联系人!',
                        type: 'warning'
                    });
                    return false
                }
                if (phoneNum==''){
                    this.$message({
                        message: '请填写手机号!',
                        type: 'warning'
                    });
                    return false
                }
                if (email==''){
                    this.$message({
                        message: '请填写邮箱号!',
                        type: 'warning'
                    });
                    return false
                }
                var strReg="";
                var r;
                var strText=this.form.email;
                strReg=/^\w+((-\w+)|(\.\w+))*\@{1}\w+\.{1}\w{2,4}(\.{0,1}\w{2}){0,1}/ig;
                r=strText.search(strReg);
                console.log(r)
                if (Number(r) == -1){
                    this.$message({
                        message: '请输入合法的邮箱',
                        type: 'warning'
                    });
                    return false
                }
                if (endDate==''){
                    this.$message({
                        message: '请选择需求截止时间!',
                        type: 'warning'
                    });
                    return false
                }
                this.$axios.post('http://192.168.1.104:8088/demands/addDemands', this.$qs.stringify({
                    companyName:companyName,
                    demandType:demandType,
                    cooperationType:cooperationType,
                    demandIndustry:demandIndustry,
                    demandIndustryDetail:demandIndustryDetail,
                    city:city,
                    district:district,
                    demandOutline:demandOutline,
                    demandContent:demandContent,
                    expectedResult:expectedResult,
                    contact:contact,
                    phoneNum:phoneNum,
                    email:email,
                    preInvestmentAmount:preInvestmentAmount,
                    endDate:'2019/09/24 18:00:00',
                    remark:remark,
                })).then(function(res) {
                    if (res.data.code==0){
                        that.$message({
                            showClose: true,
                            message: '恭喜，提交成功!',
                            type: 'success'
                        });
                    }else {
                        that.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    console.log(res)
                })
            },
            handleChange (value) {
                var arr = [];
                value.forEach((item,index)=>{
                    arr.push(CodeToText[item])
                })
                this.form.city = arr;
                console.log(this.form.city)
            },
            handleChange1(){

            }
        }
    }
</script>

<style  lang="less">
   .el-form-item__content {
     margin-left: 170px!important;
   }
   .el-form-item__label {
     width: 150px!important;
   }
   .needForm {
     width: 685px;
     margin: 0 auto;
     .title {
          font-size: 18px;
          color: #333333;
          margin-top: 76px;
          margin-bottom: 56px;
          font-weight: 600;
          text-align: center;
     }
   }
  .needWarp {
    text-align: center;
    vertical-align: top;
    .warpL {
      width: 150px;
      height: 100%;
      display: inline-block;
      background-color: green;
       ul {
         li {
           list-style: none;
         }
       }
    }
    .warpR {
      width: 490px;
      height: 100%;
      display: inline-block;
      background-color: blue;
      vertical-align: top;
       ul {
         li {
           text-align: left;
           list-style: none;
         }
       }
    }
  }
</style>



